import React from 'react'
import Layout from '../components/Layout'
import '../components/Layout.css'
import Progress from 'react-progressbar';
import p from "../resource/p1.png"
import pdf1 from "../pdf/DOC.pdf"
import pdfico from "../resource/pdf-icon.png"
import placeholder from "../resource/p.jpg"


import part2 from "../renders/2.png"
import part3 from "../renders/3.png"
import part4 from "../renders/4.png"

const p2 = ({ location }) => {
    return (
        <Layout>
            <div class="jutifycenter">
                <div class="shaded">
                    <h1>Second Stage Piston-2</h1>
                    <div>
                        <div class="by3grid">
                            <img src={part2} class="fullwide"></img>
                            <img src={part4} class="fullwide"></img>
                            <img src={part3} class="fullwide"></img>
                        </div>
                    </div>



                    <p>Part ID: 99999999</p>
                    <p>Contract Number: 99999999</p>
                    <p>Project Number: 99999999</p>
                    <p>Compressor: MK2</p>
                    {/*}
                    <p>Product Date: Dec-2019</p>
    */}
                    <div>
                        <h3>Material Supplying-(37%)</h3>
                        <Progress class="progressbar" completed={37} />
                    </div>
                    <p>Supply due jan - 2020</p>

                    <a href="../p2/tracking" target="_self" rel="noreferrer">
                        <button class="button">Tracking</button>
                    </a>

                    <div class="by4grid">

                        <a href="../requestRecived" target="_self" rel="noreferrer">
                            <button class="button">Request  Installation</button>
                        </a>
                        <a href="../requestRecived" target="_self" rel="noreferrer">
                            <button class="button">Request Inspection</button>
                        </a>

                    </div>
                </div>
            </div>


        </Layout>
    )
}
export default p2;